<template>
  <div id="pageTools" v-if="display">
    <ul>
      <li v-for="(key, index) of pageList" :key="index">
        <span
          @click="changePage(key.pageNum)"
          v-bind:class="{selectOn: key.text==nowPage}"
        >{{key.text}}</span>
      </li>
    </ul>
  </div>
</template>
<script>
let axios = require("axios");
export default {
  name: "pageTools",
  data() {
    return {
      total: 0,
      nowPage: 1,
      limit: 3,
      pageList: [],
      display: true,
      tagId : ""
    };
  },
  methods: {
    refresh: function() {
      let totalPage = Math.ceil(this.total / this.limit);
      this.pageList = [];
      this.pageList.push({ text: "首页", pageNum: 1 });
      if (this.nowPage - 2 > 0) {
        this.pageList.push({
          text: this.nowPage - 2,
          pageNum: this.nowPage - 2
        });
      }
      if (this.nowPage - 1 > 0) {
        this.pageList.push({
          text: this.nowPage - 1,
          pageNum: this.nowPage - 1
        });
      }
      this.pageList.push({
        text: this.nowPage,
        pageNum: this.nowPage
      });
      if (this.nowPage + 1 <= totalPage) {
        this.pageList.push({
          text: this.nowPage + 1,
          pageNum: this.nowPage + 1
        });
      }
      if (this.nowPage + 2 <= totalPage) {
        this.pageList.push({
          text: this.nowPage + 2,
          pageNum: this.nowPage + 2
        });
      }
      this.pageList.push({ text: "尾页", pageNum: totalPage });
    },
    getBlogsCount() {
      axios({
        url: "/getTotalBlogs",
        method: "get"
      }).then(resp => {
        this.total = resp.data.data[0].count;
        this.refresh();
      });
    },
    // 此函数调用时的tagId可能与blogList中的tagId不相同
    getBlogsByTagCount(tagId) {
      axios({
        url: "/getBlogsByTagMappingCount?tagId=" + tagId,
        method: "get"
      }).then(resp => {
        this.total = resp.data.data[0].count;
        this.refresh();
      });
    },
    getBlogs() {
      axios({
        url:
          "/getBlogByPage?offset=" +
          (this.nowPage - 1) * this.limit +
          "&limit=" +
          this.limit,
        method: "get"
      }).then(resp => {
        for (let i = 0; i < resp.data.data.length; i++) {
          // 注意，分割的为中文逗号
          resp.data.data[i].tags = resp.data.data[i].tags.split("，");
          resp.data.data[i].link = "/blog_detail?bid=" + resp.data.data[i].id;
        }
        this.$root.$emit("changeBlogs", resp.data.data);
        // blogList.blogs = resp.data.data;
      });
    },
    getBlogsByTag() {
      axios({
        url:
          "/queryBlogsByTagsByPage?tagId=" +
          this.tagId +
          "&offset=" +
          (this.nowPage - 1) * this.limit +
          "&limit=" +
          this.limit,
        method: "get"
      }).then(resp => {
        for (let i = 0; i < resp.data.data.length; i++) {
          // 注意，分割的为中文逗号
          resp.data.data[i].tags = resp.data.data[i].tags.split("，");
          resp.data.data[i].link = "/blog_detail?bid=" + resp.data.data[i].id;
        }
        this.$root.$emit("changeBlogs", resp.data.data);
        // blogList.blogs = resp.data.data;
        // console.log(blogList.blogs);
      });
    }
  },
  computed: {
    changePage: function() {
      return function(pageNum) {
        this.tagId = '';
        // 获取url
        this.nowPage = pageNum;
        if(this.$route.query.tagId){
          this.tagId = this.$route.query.tagId;
        }
        
        if (this.tagId == "") {
          this.getBlogs();
        } else {
          this.getBlogsByTag();
        }
      };
    }
  },
  created() {
    // 绑定修改total的方法，给blogList请求blogs时调用
    this.$root.$on('changeTotal', (isTag, tagId)=>{
      if(!isTag){
        this.getBlogsCount();
      }else{
        this.getBlogsByTagCount(tagId);
      }
    })
    this.$root.$on('pageShow', (showPage)=>{
      this.display = showPage;
    })
    if(this.$route.query.tagId){
      this.tagId = this.$route.query.tagId;
    }
    // 设置blog总数
    if (this.tagId == "") {
      this.getBlogsCount();
    } else {
      this.getBlogsByTagCount(this.tagId);
    }
  }
};
</script>
<style scoped>
#pageTools {
  position: relative;
  display: block;
  width: 100%;
  margin: 20px 0px 0px 0px;
}
#pageTools > ul {
  position: relative;
  display: block;
  width: 100%;
}

#pageTools > ul > li {
  opacity: 0.8;
  position: relative;
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  /* border: 1px solid; */
  border-radius: 4px;
  cursor: pointer;
  background: #eee;
  overflow: hidden;
  margin-left: 5px;
  box-shadow: 3px 3px 3px;
}
#pageTools > ul > li > span {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 100%;
}
.selectOn {
  background: #008c8c;
  color: white;
}
.selectOff {
  background: white;
  color: #000;
}
</style>